@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
  background-color: #212426;
}
:root {
  --font-roboto: "Roboto Slab", serif;
  --font-raleway: "Raleway", sans-serif;
  --primary-color :rgba(249, 211, 180, 1); ;
  --logo-background: linear-gradient(
    90deg,
    rgba(249, 211, 180, 1) 40%,
    rgba(249, 211, 180, 0) 100%
  );
  --background-2 : #1f2123;
}

.all{
  width: 100%;
  position: absolute;
  top: 50px;
  display: flex;
  justify-content: center;
}
.logo{
  font-size: 30px;
  letter-spacing: 0.9px;
  background: linear-gradient(
    90deg,
    rgba(249, 211, 180, 1) 40%,
    rgba(249, 211, 180, 0) 100%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: fit-content;
}
.bar{
  background: #212426;
  border-bottom: 1px solid rgba(128, 128, 128, 0.397);
  width: 100%;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  height: 50px;
  padding-inline: 20px;
  align-items: center;
  color: rgba(249, 211, 180, 1);
  position: fixed;
  top: 0;
}
.menu{
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  position: relative;
}


.menuItem {
  list-style: none;
  margin-inline:20px ;
  cursor: pointer;
  letter-spacing: 0.9px;
  background: linear-gradient(
    90deg,
    rgba(249, 211, 180, 1) 90%,
    rgba(249, 211, 180, 0) 100%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: fit-content;
}
.menuItem:hover{
  background: linear-gradient(
    90deg,
    rgba(249, 211, 180, 1) 0%,
    rgba(249, 211, 180, 0) 100%
  );
  background-clip: text;
}
.lines{
  display: none;
  flex-direction: column;
  cursor: pointer;
}
.line{
  width: 20px;
  margin: 2px;
  height: 2px;
  background: rgba(249, 211, 180, 1);
  border-radius: 1px;
  transition: 0.5s ease-in-out;
}



h1 {
  font-size: 3rem;
  letter-spacing: 0.9px;
  background: linear-gradient(
    90deg,
    rgba(249, 211, 180, 1) 0%,
    rgba(249, 211, 180, 0) 100%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: fit-content;
}
.search-title{
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 3%;

}
.search {
  width: 71%;
  margin: 4rem 0 2rem;
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #1f2123;
  -webkit-box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
  box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
}

.search input {
  flex: 1;
  border: none;
  font-size: 1.5rem;
  font-family: var(--font-raleway);
  font-weight: 500;
  padding-right: 1rem;

  outline: none;
  color: #a1a1a1;
  background: #1f2123;
}

.search img {
  width: 35px;
  height: 35px;
  cursor: pointer;
}
.empty {
  display: flex;
  justify-content: center;
  align-items: center;
}

.empty h2 {
  font-size: 1.25rem;
  color: #f9d3b4;
  font-family: var(--font-raleway);
}

.container {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.movie {
  width: 310px;
  height: 460px;
  margin: 1.5rem;
  position: relative;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.movie div:nth-of-type(1) {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
  color: #f9d3b4;
}

.movie:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
  z-index: 11;
}

.movie div:nth-of-type(2) {
  width: 100%;
  height: 100%;
}

.movie div:nth-of-type(2) img {
  height: 100%;
  width: 100%;
}

.movie div:nth-of-type(3) {
  z-index: 2;
  background-color: #343739;
  padding: 16px 24px 24px 24px;

  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.movie div:nth-of-type(3) span {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #f0f0f0;
}
.movie-title{
  position: absolute;
  top: 10%;
  text-align: center;
  left: 5%;
  opacity: 0;
  background: none;
}

.movie:hover .movie-title {
  margin-top: 5px;
  font-family: "Roboto Slab", serif;
  color: #f9d3b4;
  opacity: 1;
}

.movie:hover div:nth-of-type(2) {
  height: 100%;
  opacity: 0.3;
}

.movie:hover div:nth-of-type(3) {
  background-color: transparent;
}

.movie:hover div:nth-of-type(1) {
  opacity: 1;
}
@media (max-width: 600px) {
  .lines{
    display: flex;
  }
  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }
  .menu{
    display: none;
    flex-direction: column;
    position: absolute;
    top: 50px;
    height: 100vh;
    width: 100%;
    background: #212426;
    right: 0;
    animation: movingMenu 1s ease-in-out forwards;
  }
  .menuItem{
    margin: 20px;
    left: 0px;
  }
  
  @keyframes movingMenu {
    from {
      opacity: 0;
      transform: translateX(100%);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
}
